import React from 'react';
import {useField} from 'formik';
import s from './Input.module.scss';

function Input ({number, title, ...props}) {
  const [field, meta] = useField(props);

  return (
    <div className={`${s.input} form-group`}>
      <label htmlFor={title}>{title}</label>
      <input
        className={`${meta.error && meta.touched && 'is-invalid'} form-control`}
        type="text"
        {...props}
        {...field}
      />
      {meta.touched && meta.error && <small className="text-danger">{meta.error}</small> }
      {number && <small id="phoneHelp" className="form-text text-muted"> Enter phone number in open format</small> }
    </div>
  )
}

export default Input;