    import axios from 'axios';

const DEFAULT_URL = "https://globalmap.github.io/abz/api"

const Api = {
     Auth: {
        _token: '',
    
        _setToken(token) {
            this._token = token;
        },
    
        getToken() {
            axios.get(`${DEFAULT_URL}/auth/token`)
                .then(res => {
                    if(res.data.success) {
                        this._setToken(res.data.token)
                        console.log(`token ${this._token}`)
                    }
                })
        },
    
        registerUsers(position, name, email, phone, image) {
            
            const formData = new FormData();
            formData.append('position_id', position);
            formData.append('name', name);
            formData.append('email', email);
            formData.append('photo', image[0]);
            formData.append('phone', phone);

            axios({
                url: `${DEFAULT_URL}/auth/users`,
                method: 'POST',
                data: formData,
                headers: {
                    'Token':  this._token,
                }
            })
        }
    },
    
    Users: {
        getPosition() {
            return axios.get(`${DEFAULT_URL}/positions`)
        },
        getUsers(page, count) {
            return axios.get(`${DEFAULT_URL}/users?page=${page}&count=${count}`)
        },
    
        getUserById(id) {
            return axios.get(`${DEFAULT_URL}/users/${id}`)
        }
    }
}

export default Api;