import React, { useEffect, useState } from 'react';
import {Container} from 'react-bootstrap';
import s from './Block3.module.scss';
import adapt from './Adaptation.module.scss';
import Button from '../../components/Button/Button';
import Card from './Card/Card';
import Api from '../../Api/Api';

function Block3 () {
  const [page, setPage] = useState(1);
  const [users, setUsers] = useState([]);
  const [totalPages, setTotalPages] = useState(0);
  const [count, setCount] = useState(6);

  useEffect(() => {
    if(window.screen.width <= 767) {
      setCount(3)
      Api.Users.getUsers(page, 3)
        .then(res => setUsers(res.data.users))
    } else {
      Api.Users.getUsers(page, 6)
        .then(res => setUsers(res.data.users))
    }

    
  }, [])

  const getMoreUsers = (page) => {    
    Api.Users.getUsers(page, count)
    .then(res => {
      setTotalPages(res.data.total_pages)
      setUsers(users.concat(res.data.users));
    })
    setPage(page);
  }

  return (
    <Container 
      fluid
      className={`
        ${s.main}
        ${adapt.main}
    `}>
      <h1 className="text-center">Our cheerful users</h1>
      <p className="text-center text-secondary">Attention! Sorting users by registration date</p>

      <div className={`
        ${s.Usercards}
        ${adapt.Usercards}
        d-flex flex-wrap
        justify-content-md-between
        justify-content-lg-between
        justify-content-xl-center
        justify-content-center
        pl-3 pr-3
      `}>     
        {users.map((data, index) => (
           <Card
             key={index}
             name={data.name}
             phone={data.phone}
             photo={data.photo}
             position={data.position}
             email={data.email}
           />
         ))}
      </div>

      <button
        className={`
          ${s.button}
          ${adapt.button}
          btn
          btn-customPrimary
          align-center
          font-weight-bold
          mt-4
        `}
        onClick={
          () => page !== totalPages ? getMoreUsers(page + 1) : null
        }
      >
        Show more
      </button>
    </Container>
  )
}

export default Block3;