import React from 'react';
import s from './Button.module.scss';

function Button ({text, width, ...props}) {
    return (
      <button
        className={s.main}
        style={{width: width}}
        {...props}
      >
        {text}
      </button>
    )
}

Button.defaultProps = {
  width: "40%"
};


export default Button;