import React from 'react';
import { Modal } from 'react-bootstrap';

function ModalWindow(props) {
	return (
		<Modal
			{...props}
			dialogClassName="modalSize"
			aria-labelledby="contained-modal-title-vcenter"
			centered
		>
			<Modal.Header closeButton>
				<Modal.Title id="contained-modal-title-vcenter">Congratulations</Modal.Title>
			</Modal.Header>
		<Modal.Body>You have succefully passed the registration</Modal.Body>
		<Modal.Footer>
			<button className="btn btn-customPrimary" onClick={props.onHide}>Great</button>
		</Modal.Footer>
		</Modal>)
}

export default ModalWindow;