import React from 'react';
import s from './SidebarUI.module.scss';
import Blocks from './Blocks/Blocks';
import Logo from '../../Assets/logo.svg';

function SideBarUI() {
	const itemsNavBlock1 = [
		"About me",
		"Relationships",
		"Users",
		"Sign Up",
		"Terms and Coditions"
	];
	const itemsNavBlock2 = [
		"How it works",
		"Partnership",
		"Help",
		"Leave testimonial",
		"Contac us"
	];
	const itemsNavBlock3 = [
		"Articles",
		"Our news",
		"Testimonials",
		"Licenses",
		"Privacy Policy"
	];
	return (
		<>
			<div className={`${s.logoBlock} pl-3 pt-1 `}>
				<img src={Logo} />
			</div>

			<Blocks items={itemsNavBlock1} />
			<Blocks items={itemsNavBlock2} />
			<Blocks items={itemsNavBlock3} />			
		</>
	)
}

export default SideBarUI;