import React, {useState, useEffect} from 'react';
import s from './Form.module.scss';
import adapt from './Adaptation.module.scss';
import { Formik, Form } from 'formik';
import Api from '../../../Api/Api';
import Input from './components/Input/Input';
import RadioButtons from './components/RadioButtons/RadioButtons';
import InputFile from './components/InputFile/InputFile';
import { registerSchema } from './components/validationSchema';
import ModalWindow from '../../../components//Modal/Modal';

function RegisterForm ({onSubmit}) {
  const [showModal, setShowModal] = useState(false);
  const [radioText, setRadioText] = useState([]);

  useEffect(() => {
    Api.Users.getPosition()
      .then((res) => setRadioText(res.data.positions))
  }, [])

  const handleShowModal = () => setShowModal(true);

  const formikProps = {
    initialValues: {
      name: '',
      email: '',
      phone: '',
      position: '',
      photo: undefined
    },
    onSubmit: value => {
      Api.Auth.registerUsers(value.position, value.name, value.email, value.phone, value.photo)
      handleShowModal();
    }
  };
  return (
      <div className={`${s.main} ${adapt.main}`}>
          <Formik
            validationSchema={registerSchema}
            {...formikProps}
          >
          <Form className={`${s.form} ${adapt.form}`}>
                <Input
                  title="Name"
                  name="name"
                  placeholder="Your name"
                />

                <Input
                  title="Email"
                  name="email"
                  placeholder="Your email"
                />

                <Input
                  title="Phone number"
                  name="phone"
                  placeholder="+380 XX XXX XX XX"
                  number={true}
                  aria-describedby="phoneHelp"
                />

                <div className={`${s.radioButtons} ${adapt.radioButtons}`}>
                  <p>Select your position</p>

                  {
                    radioText.map((data, index) => (
                      <RadioButtons
                        key={index}
                        id={data.id}
                        text={data.name}
                      />
                    ))
                  }
                  
                </div>

                <InputFile />

                <button
                  type="submit"
                  className="btn btn-customPrimary"
                >
                  Sign up now
                </button>

                <ModalWindow 
                  show={showModal} 
                  onHide={() => setShowModal(false)} 
                />
              </Form>
          </Formik>
      </div>
  )
}

export default RegisterForm;