import React, { useEffect } from 'react';
import {useField} from 'formik';
import s from './InputFile.module.scss';

function InputFile (props) {
  const [field, meta, helpers] = useField('photo');
  const { setValue } = helpers;
  
  useEffect(() => {
    const readFileBtn = document.getElementById("file");
    const customTxt = document.getElementById("file-custom");

    readFileBtn.addEventListener("change", () => {
      if(readFileBtn.value) {
        setValue(readFileBtn.files)
        console.log(readFileBtn.files)
        customTxt.innerHTML = readFileBtn.value.match(/[\/\\]([\w\d\s\.\-\(\)]+)$/)[1];
      } else {
        customTxt.innerHTML = "Upload your photo";
      }
    })
  }, [])

  return (
   <div className={`${s.item} custom-file ${meta.error && meta.touched && 'is-invalid'}`}>
    <p>Photo</p>
    <input id="file" type="file" name="photo" className="custom-file-input" accept="image/jpeg,image/jpg" />
    <label id="file-custom" className="custom-file-label" htmlFor="file">Choose file</label>
    {meta.touched && meta.error && <small className="text-danger">{meta.error}</small> }
  </div>
  )
}

export default InputFile;