import React from 'react';
import {useField} from 'formik';
import s from './RadioButtons.module.scss';

function RadioButtons ({text, id}) {
	const [field, meta, helpers] = useField("position");
	const { setValue } = helpers;
	
	return (
		<div className={`${s.item} custom-control custom-radio`}>
		<input 
			className="custom-control-input" 
			type="radio" name="position" 
			id={text} 
			onClick={() => setValue(id)}
		/>
		<label
			className="custom-control-label"
			htmlFor={text}
		>
			{text}
		</label>
		</div>
	)
}

export default RadioButtons;