import React from 'react';
import s from '../Block3.module.scss';
import adapt from '../Adaptation.module.scss';
import ReactTooltip from "react-tooltip";

function Card (props) {
    return (
        <div className={`
        	${s.card}
            ${adapt.card}
        	ml-5
        	ml-sm-0
        	mr-5
        	mr-sm-0
        	mr-lg-5
        	ml-lg-5`
        }>
            <img
            	className="rounded-circle"
            	src={props.photo}
            	alt="user_photo"
            />
            <h2>
            	{props.name}
            </h2>
            <p className={s.specialist}>
            	{props.position}
            </p>
            <p 
            	data-tip
            	data-for={`tooltip-${props.email}`}
            	className={s.email}
            >
            	{props.email}
            </p>
            <ReactTooltip
            	id={`tooltip-${props.email}`}
            	place="bottom"
            	effect="solid"
            >
            	<span>{props.email}</span>
            </ReactTooltip>
            <p className={s.number}>
            	{props.phone}
            </p>
        </div>
    )
}

export default Card;